___CSS_LOADER_EXPORT___.push([module.id, `.bluebook-button {
  font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 300;
  border: 0px;
  cursor: pointer;
  display: inline-block;
  line-height: 1;
}

.bluebook-button--primary {
  color: white;
  background-color: #3364E0;
  border-radius: 7px;
}

.bluebook-button--secondary {
  color: #3364E0;
  border: 1px solid;
  border-radius: 7px;
  border-color: #3364E0;
}

.bluebook-button--tertiary {
  color: #3364E0;
  background-color: white;
  border:none;
}

.bluebook-button--small {
  font-size: 12px;
  padding: 10px 16px;
}
.bluebook-button--medium {
  font-size: 14px;
  padding: 11px 20px;
}
.bluebook-button--large {
  font-size: 16px;
  padding: 12px 24px;
}
